import React from 'react';
import styles from './Cantacts.module.css';

function Cantacts() {
  return (
    <div className={styles.cantacts}>
      <div className={styles.container}>
        <div className={styles.myCantact}>Contacts</div>
        <form className={styles.form}>
          <input type='text' placeholder="Name"></input>
          <input type='text' size='40px' placeholder="Email"></input>
          <textarea rows="10" cols="60" name="text" placeholder="Your message"></textarea>
        </form>
        <button className={styles.send}>Submit</button>
      </div>
    </div>
  );
}

export default Cantacts;